*{
  margin: 0px;
  padding: 0px;
}
.main{
  text-align: center;
}

h2{
  background-color: rgb(253, 92, 63);
  font-size: 30px;
  margin-bottom:50px;
}
h3{
      margin-bottom: 50px;
      color:blue;
}

.container{
  text-align: center;

}

#input{
  height: 22px;
  border-radius: 4px 0px 0px 4px;
  border: none;
  border: 1px solid black;
  padding-left:10px;
}
.add{
  height: 24px;
  background-color: #18ca53;
  border:none;
  outline: none;
  color: white;
  padding-left: 10px;
  padding-right: 10px;
  border-radius:0 4px 4px 0px; 
}
.add:active{
  background-color: #10943c;
}
#input,#parent{
  width:40%;
  margin: auto;
  margin-bottom: 30px;
}


.item{

  margin-bottom: 15px;
  text-align: left;
  padding-left: 5px;
  height: 20px;
  padding-bottom: 5px;
  box-shadow: 1px 1px 2px rgb(211, 209, 209),-1px -1px 2px rgb(211, 209, 209);
}

.item .delete{
  float: right;
  height: 27px;
  background-color: #f34040;
  border:none;
  outline: none;
  color: white;
  padding-left: 10px;
  padding-right: 10px;
  position: relative;
  left:1px;
  top:-1px;
  border-radius:2px;
}